import React, {useState} from 'react'
import useIsMobile from '../hooks/isMobile'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'

import { Swiper, SwiperSlide } from 'swiper/react'
import { Autoplay, FreeMode, Thumbs, EffectFade, EffectCreative, Mousewheel } from 'swiper'
import 'swiper/css'
import 'swiper/css/autoplay'
import 'swiper/css/free-mode'
import 'swiper/css/thumbs'
import 'swiper/css/effect-fade'
import "swiper/css/effect-creative"

const HelpSlider = () => {
    const {mobile} = useIsMobile('991px')
    const [thumbsSwiper, setThumbsSwiper] = useState(null)

    if (mobile) {
        return (
            <div>
                <h3 className='mb-4'>   </h3>
                <Swiper
                    className='help-slider'
                    grabCursor={true}
                    effect={"creative"}
                    direction={"vertical"}
                    mousewheel={true}
                    creativeEffect={{
                    prev: {
                        translate: [0, 0, -400],
                    },
                    next: {
                        translate: [0, "100%", 0],
                    },
                    }}
                    modules={[EffectCreative, Mousewheel]}
                >
                    <SwiperSlide>
                        <h4> ,    .             </h4>
                        <img src="imgs/img2.jpg" alt=" ,    .             " />
                    </SwiperSlide>
                    <SwiperSlide>
                        <h4> ,      </h4>
                        <img src="imgs/img3.jpg" alt=" ,      " />
                    </SwiperSlide>
                    <SwiperSlide>
                        <h4>  .   ,       </h4>
                        <img src="imgs/img4.jpg" alt="  .   ,       " />
                    </SwiperSlide>
                    <SwiperSlide>
                        <h4>  .      ,   ,    </h4>
                        <img src="imgs/img5.jpg" alt="  .      ,   ,    " />
                    </SwiperSlide>
                </Swiper>
            </div>
        )
    } else return (
        <Row>
            <Col lg={6} xxl={7}>
                <h3 className='mb-4'>   <br /> </h3>
                <Swiper
                    direction="vertical"
                    spaceBetween={30}
                    slidesPerView={'auto'}
                    freeMode={true}
                    watchSlidesProgress={true}
                    modules={[FreeMode, Thumbs, Autoplay]}
                    className="thumbslides"
                    onSwiper={setThumbsSwiper}
                    autoplay={{
                        delay: 3000,
                        disableOnInteraction: false,
                        waitForTransition: true
                    }}
                >
                    <SwiperSlide>
                        <div className="indicator"></div>
                        <p> ,    .             </p>
                    </SwiperSlide>
                    <SwiperSlide>
                        <div className="indicator"></div>
                        <p> ,      </p>
                    </SwiperSlide>
                    <SwiperSlide>
                        <div className="indicator"></div>
                        <p>  .   ,       </p>
                    </SwiperSlide>
                    <SwiperSlide>
                        <div className="indicator"></div>
                        <p>  .      ,   ,    </p>
                    </SwiperSlide>
                </Swiper>
            </Col>
            <Col lg={6} xxl={5}>
                <Swiper
                    spaceBetween={20}
                    thumbs={{
                        swiper: thumbsSwiper && !thumbsSwiper.destroyed ? thumbsSwiper : null,
                    }}
                    modules={[FreeMode, Thumbs, Autoplay, EffectFade]}
                    className="thumbs-main"
                    effect="fade"
                    fadeEffect={{ crossFade: true }}
                    autoplay={{
                        delay: 3000,
                        disableOnInteraction: false,
                        waitForTransition: true
                    }}
                    breakpoints={{
                        768: {
                            pagination: false,
                        },
                    }}
                >
                    <SwiperSlide>
                        <img src="imgs/img2.jpg" alt=" ,    .             " />
                    </SwiperSlide>
                    <SwiperSlide>
                        <img src="imgs/img3.jpg" alt=" ,      " />
                    </SwiperSlide>
                    <SwiperSlide>
                        <img src="imgs/img4.jpg" alt="  .   ,       " />
                    </SwiperSlide>
                    <SwiperSlide>
                        <img src="imgs/img5.jpg" alt="  .      ,   ,    " />
                    </SwiperSlide>
                </Swiper>
            </Col>
        </Row>
    );
};

export default HelpSlider;