import React from 'react'
import Container from 'react-bootstrap/Container'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
import { RxCaretDown } from "react-icons/rx"
import AppLinks from './AppLinks'
import useIsMobile from '../hooks/isMobile'
import { SlBubbles, SlGrid, SlUser } from "react-icons/sl"
import { TfiPencilAlt } from "react-icons/tfi";

const Footer = () => {
    const {mobile} = useIsMobile('991px')

    return (
        <>
        <footer>
            <Container>
                <Row xs={1} xl={2}>
                    <Col>
                        <nav className='menu'>
                            <ul>
                                <li><a href="/"> </a></li>
                                <li><a href="/"></a></li>
                                <li><a href="/"> </a></li>
                            </ul>
                            <ul>
                                <li><a href="/">  </a></li>
                                <li><a href="/">  </a></li>
                                <li><a href="/"> </a></li>
                            </ul>
                            <ul>
                                <li><a href="/"></a></li>
                                <li><a href="/"> </a></li>
                                <li><a href="/"> </a></li>
                            </ul>
                        </nav>
                    </Col>
                    <Col className='mt-4 mt-xl-0'>
                        <AppLinks/>
                    </Col>
                </Row>
                <hr />
                <div className='bottom'>
                    <button type='button'>
                        <span></span>
                        <RxCaretDown className='fs-15'/>
                    </button>
                    <a href="/"> </a>
                    <a href="/"> </a>
                    <span> 20222022   </span>
                </div>
            </Container>
        </footer>
        {
            (mobile === true) &&
            <footer className="mobile">
                <Container>
                    <nav>
                        <ul className="list-unstyled">
                            <li>
                                <a href="/" className='active'>
                                    <SlGrid/>
                                    <span></span>
                                </a>
                            </li>
                            <li>
                                <a href="/">
                                    <TfiPencilAlt/>
                                    <span></span>
                                </a>
                            </li>
                            <li>
                                <a href="/">
                                    <SlBubbles/>
                                    <span></span>
                                </a>
                            </li>
                            <li>
                                <a href="/">
                                    <SlUser/>
                                    <span></span>
                                </a>
                            </li>
                        </ul>
                    </nav>
                </Container>
            </footer>
        }
        </>
    );
};

export default Footer;